<template>
  <v-card flat>
    <v-card-text>
      <v-container fluid grid-list-md>
        <v-layout row wrap>
          <v-flex xs12>
            <v-img :src="detail.url"></v-img>
          </v-flex>
          <v-flex xs12>
            <p class="headline">{{detail.title}}</p>
          </v-flex>
          <v-flex md8>
            <material-card color="info" title="">
              <v-list three-line>
                <template v-for="(item, index) in comments">
                  <v-subheader v-if="item.username" :key="item.username">{{ item.username }}:</v-subheader>
                  <v-list-tile :key="item.username" avatar>
                    <v-list-tile-content>
                      <v-list-group v-text="item.content"></v-list-group>
                      <v-list-tile-sub-title v-text="item.cdate"></v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider :key="index" :inset="true"></v-divider>
                </template>
              </v-list>
              <v-text-field label="" v-model="input"></v-text-field>
              <v-btn color="pink" @click="submit">
                <v-icon>edit</v-icon>
              </v-btn>
            </material-card>
          </v-flex>
          <v-flex md4>
            <v-layout row wrap class="text-xs-left">
              <v-flex md6>
                <v-btn flat icon color="pink">
                  <v-icon>star</v-icon>
                  :{{detail.star}}
                </v-btn>
              </v-flex>
              <v-flex md6>
                <v-btn flat icon color="lightblue">
                  <v-icon>thumb_up</v-icon>
                  :{{detail.thumb}}
                </v-btn>
              </v-flex>
              <v-flex md2>
                <v-icon small>loyalty</v-icon>
              </v-flex>
              <v-flex v-for="tag in detail.tags" :key="tag" md3>{{tag}}</v-flex>
              <v-flex md12>
                <v-divider></v-divider>
              </v-flex>
              <v-flex md3></v-flex>
              <v-flex md9>
                <div>{{detail.author}}</div>
              </v-flex>
              <v-flex md3></v-flex>
              <v-flex md9>:{{detail.upload}}</v-flex>
              <v-flex md3></v-flex>
              <v-flex md9>:{{detail.count}}</v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    detail: {
      author: "",
      count: 0,
      star: 0,
      thumb: 0,
      title: "",
      upload: "",
      url: "",
      tags: []
    },
    input: "",
    comments: [
      {
        username: "xxx",
        content: "",
        cdate: "",
      }
    ]
  }),
  props: {
    pid: Number
  },
  methods: {
    // 
    submit() {
      this.input;
    }
  },

  created: function() {
    //  url
    this.$api.image
      .getImageDetails({
        iid: this.pid
      })
      .then(res => {
        var data = res.data;
        data.url = this.$api.root.getUrl(data.url);
        this.detail = data;
      })
      .catch(err => {});
    // 
    this.$api.image
      .getCommentsByiid({
        iid: this.pid
      })
      .then(res => {
        console.log(res)
        this.comments = res.data.comments
      })
      .catch(err => {});
  }
};
</script>
